/**
 * DetailForm.js
 * Wrapper that contains the piggy login and the
 * right container to checkout or choose plan
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import $ from 'jquery';
import window from 'global/window';
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { Row, Col } from 'reactstrap';

import LabelImg from 'assets/images/forms/Label.svg';

// Import Insurer Logos
import PiggyFillForm from 'components/Templates/PiggySignUp/PiggyFillFormNew';
import ChangeInitialFormDataNew from 'components/Templates/ChangeInitialFormData/ChangeInitialFormDataNew';

import Button from 'components/FormsV3/Button/Button';
import SingleCheckBox from 'components/FormsV3/SingleCheckBox/SingleCheckBox';

import './DetailFormNew.scss';

class DetailFormNew extends Component {
  constructor(props) {
    super(props);

    this.state = {
      rightStickyStyle: 'right-scroll',
      isSignUp: false,
    };

    this.handlePreSubmit = this.handlePreSubmit.bind(this);
    this.handleScroll = this.handleScroll.bind(this);
    this.renderSignUp = this.renderSignUp.bind(this);
    this.renderSignUpButton = this.renderSignUpButton.bind(this);
    this.renderUpperRightContainer = this.renderUpperRightContainer.bind(this);
    this.renderBottomContainer = this.renderBottomContainer.bind(this);
  }

  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll);
    window.scrollTo(0, 0);
    this.handleScroll();

    if ($('#intercom-container')) {
      $('#intercom-container').hide();
    }
  }

  componentWillUnmount() {
    if ($('#intercom-container')) {
      $('#intercom-container').show();
    }

    window.removeEventListener('scroll', this.handleScroll);
  }

  handlePreSubmit(e) {
    const { isSignUp } = this.state;
    const { onSubmit, login } = this.props;
    if (isSignUp) {
      login.loginContainer.setSuccessCallback(() => onSubmit(e));
      login.loginContainer.setInitialIndex(2);
      login.popup.openPopup();
      return;
    }
    onSubmit(e);
  }

  handleScroll() {
    if (this.upperMarker) {
      const { rightStickyStyle } = this.state;
      const stickyStyle = this.upperMarker.getBoundingClientRect().y < 84 ? 'right-sticky' : 'right-scroll';

      if (rightStickyStyle !== stickyStyle) {
        this.setState({
          rightStickyStyle: stickyStyle,
        });
      }
    }
  }

  renderSignUp() {
    const {
      isLoggedIn,
      login,
      piggyCallback,
      category,
      planDetails,
    } = this.props;

    return (
      <div className="piggy">
        <PiggyFillForm
          isLoggedIn={isLoggedIn}
          cashback={planDetails && planDetails.cashback}
          category={category}
          onClick={() => {
            login.loginContainer.setSuccessCallback(piggyCallback);
            login.loginContainer.setInitialIndex(1);
            login.popup.openPopup();
          }}
        />
      </div>
    );
  }

  renderSignUpButton(isPlanTotal) {
    const { isSignUp } = this.state;
    const { isLoggedIn } = this.props;
    const signUpStyle = isPlanTotal ? '' : 'no-total';

    if (!isLoggedIn) {
      return (
        <div className={`sign-up ${signUpStyle}`}>
          <SingleCheckBox
            id="checkbox-detail-form"
            onChange={() => this.setState({ isSignUp: !isSignUp })}
            checked={!!isSignUp}
          >
            Sign Up
          </SingleCheckBox>
        </div>
      );
    }

    return null;
  }

  renderUpperRightContainer() {
    const { rightStickyStyle } = this.state;

    const {
      planDetails,
      checkoutButtonDesc,
      editForm,
      category,
      values,
    } = this.props;

    return (
      <Fragment>
        <div className="d-none d-lg-block">
          {this.renderSignUp()}
        </div>
        <div
          className={`upper-right-container ${rightStickyStyle}`}
        >
          <div ref={node => this.upperRightContainer = node} />
          <div className="logo-container">
            <div className="logo-inner-container">
              <img className="company-logo" src={planDetails.logo} alt={`${planDetails.logoDesc}.png`} />
            </div>
            <p className="plan-name">{planDetails.planName}</p>
            {this.renderSignUpButton(planDetails.planTotal)}
            <ChangeInitialFormDataNew
              values={values}
              displayOnly={editForm.displayOnly}
              form={category}
              headerValue={editForm.headerValue}
              valuesToListen={editForm.valuesToListen}
              conditions={editForm.conditions}
            />
          </div>
          <div className="submit-container">
            <div className="sign-up-container">
              {
                planDetails.planSubTotal ?
                  <div className="cost-container">
                    <p>
                      <span className="total-text">SubTotal</span>
                      <span className="cost-text">{planDetails.planSubTotal}</span>
                    </p>
                    <p className="gst-text">GST 7% included</p>
                  </div>
                :
                  null
              }
              {
                planDetails.discountPercentage && planDetails.discountAmount ?
                  <div className="cost-container">
                    <p>
                      <span className="total-text">Discount {planDetails.discountPercentage}</span>
                      <span className="cost-text">{planDetails.discountAmount}</span>
                    </p>
                  </div>
                :
                  null
              }
            </div>
            <div className="divider" />
            <div className="sign-up-container">
              {
                planDetails.planTotal ?
                  <div className="cost-container">
                    <p>
                      <span className="total-text">Total</span>
                      <span className="final-cost-text">{planDetails.planTotal}</span>
                    </p>
                  </div>
                :
                  null
              }
              <Button
                variant="medium-flat-green"
                height={48}
                onClick={this.handlePreSubmit}
              >
                <p>{checkoutButtonDesc}</p>
              </Button>
              {
                planDetails.cashback ?
                  <div className="cashback-earned-container">
                    <img src={LabelImg} alt="Label.svg" />
                    <span>Earn {planDetails.cashback} Cashback</span>
                  </div>
                :
                  null
              }
            </div>
          </div>
        </div>
      </Fragment>
    );
  }

  renderBottomContainer() {
    const { rightStickyStyle } = this.state;

    const {
      planDetails,
      checkoutButtonDesc,
      selectedPlanData,
      isLoggedIn,
    } = this.props;

    return (
      <Fragment>
        <div
          className={`bottom-container ${rightStickyStyle}`}
        >
          <div className="bottom-inner-container">
            {
              planDetails.planTotal ?
                <div className="price-container">
                  <h5>{planDetails.planName}</h5>
                  {selectedPlanData && selectedPlanData.planType && <h6>{_.startCase(selectedPlanData.planType)}</h6>}
                  <h4>
                    <span>${planDetails.planTotal}</span>
                    {
                      planDetails.planSubTotal && <span className="price">{planDetails.planSubTotal}</span>
                    }
                  </h4>
                </div>
              :
                null
            }
            <div className={`resp-button-container ${isLoggedIn ? 'big' : 'small'}`}>
              {this.renderSignUpButton(planDetails.planTotal)}
              <Button
                variant="medium-flat-green"
                height={48}
                onClick={this.handlePreSubmit}
              >
                <p>{checkoutButtonDesc}</p>
              </Button>
            </div>
          </div>
        </div>
      </Fragment>
    );
  }

  render() {
    const { errorArray } = this.props;
    return (
      <Fragment>
        <Row className="detail-main-container-new" noGutters>
          <div ref={node => this.upperMarker = node} />
          <Col xs={12} lg={7}>
            <div className="d-block d-lg-none">
              {this.renderSignUp()}
            </div>
            <div className="detail-left-container">
              {this.props.children}
              {errorArray}
            </div>
          </Col>
          <Col lg={5} className="d-none d-lg-block">
            {this.renderUpperRightContainer()}
          </Col>
          <Col xs={12} className="d-block d-lg-none">
            {this.renderBottomContainer()}
          </Col>
        </Row>
      </Fragment>
    );
  }
}

DetailFormNew.defaultProps = {
  errorArray: null,
  checkoutButtonDesc: 'Checkout',
  editForm: null,
  selectedPlanData: null,
  values: null,
};

DetailFormNew.propTypes = {
  onSubmit: PropTypes.func.isRequired,
  piggyCallback: PropTypes.func.isRequired,
  planDetails: PropTypes.object.isRequired,
  isLoggedIn: PropTypes.bool.isRequired,
  errorArray: PropTypes.array,
  login: PropTypes.object.isRequired,
  checkoutButtonDesc: PropTypes.string,
  category: PropTypes.string.isRequired,
  editForm: PropTypes.object,
  selectedPlanData: PropTypes.object,
  values: PropTypes.object,
};

const mapStateToProps = state => ({
  login: state.login,
});

export default connect(mapStateToProps)(DetailFormNew);
