/**
 * TravelDetail.js
 * Detail View for travel
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { Row } from 'reactstrap';
import PropTypes from 'prop-types';

import FieldGenerator from 'components/FormsV3/FieldGenerator';
import DetailForm from 'components/Templates/DetailForm/DetailFormNew';
import TabView from 'components/Templates/TabView/TabViewNew';

const defaultFormName = 'adult_0';

class TravelDetail extends Component {
  constructor(props) {
    super(props);

    this.state = {
      formName: defaultFormName,
    };

    this.generateTabs = this.generateTabs.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);

    this.renderMain = this.renderMain.bind(this);
    this.renderFields = this.renderFields.bind(this);
    this.renderForm = this.renderForm.bind(this);
  }

  async handleSubmit() {
    const { handleSubmit } = this.props;
    const errorArray = await handleSubmit(this.tabView);

    if (!_.isEmpty(errorArray)) {
      this.setState({
        errorArray,
      });
    }
  }

  generateTabs() {
    const { planData, values } = this.props;
    const name = values.plan_type.value === 'Group' ? 'Person' : 'Adult';

    const result = [];
    if (values.num_adult && values.num_adult.value) {
      for (let i = 0; i < planData.numAdults; ++i) {
        result.push({
          desc: `${name} ${i + 1}`,
          onClick: () => { this.setState({ formName: `adult_${i}` }); },
        });
      }
    }

    for (let i = 0; i < planData.numChildren; ++i) {
      result.push({
        desc: `Child ${i + 1}`,
        onClick: () => { this.setState({ formName: `child_${i}` }); },
      });
    }
    return result;
  }

  renderFields(fields, isBlk = false) {
    const { formName } = this.state;

    const {
      detailValues,
      detailErrors,
      onChangeDetailValue,
      handlePostCode,
    } = this.props;

    return (
      <Fragment>
        {
          _.map(fields, (f, index) => (
            <FieldGenerator
              key={`business-form-${index}`}
              values={detailValues[formName]}
              errors={detailErrors[formName]}
              fieldData={f}
              onChangeValue={(type, name, value) => onChangeDetailValue(type, name, value, formName)}
              extraInfo={f.extra_info && <p>{f.extra_info}</p>}
              noInternalDesc={f.no_internal_desc}
              handlePostCode={() => handlePostCode(detailValues.adult_0, onChangeDetailValue, !isBlk, 'adult_0')}
            />
          ))
        }
      </Fragment>
    );
  }

  renderMain() {
    const { formName } = this.state;
    const {
      getIndividualFields,
      adultFields,
      childFields,
      selectedPlanData,
    } = this.props;

    if (formName === defaultFormName) {
      const iFields = getIndividualFields(selectedPlanData);
      return this.renderFields(iFields.fields, iFields.isBlk);
    }

    if (_.includes(formName, 'child_')) {
      return this.renderFields(childFields);
    }

    return this.renderFields(adultFields);
  }

  renderForm() {
    const { values } = this.props;
    if (values.plan_type && values.plan_type.value === 'Individual') {
      return (
        <Row className="main-form" noGutters>
          {this.renderMain()}
        </Row>
      );
    }

    return (
      <TabView
        ref={node => this.tabView = node}
        maxTabWidthPx={128}
        className="tabview-margin-top"
        tabs={this.generateTabs()}
        isDataFadeOnly
        name="travel-detail-tab"
        scrollTabs
      >
        <Row className="main-form" noGutters>
          {this.renderMain()}
        </Row>
      </TabView>
    );
  }

  render() {
    const { errorArray } = this.state;
    const {
      isLoggedIn,
      planDetails,
      piggyCallback,
      selectedPlanData,
      values,
      valuesToListen,
      conditions,
    } = this.props;

    return (
      <DetailForm
        category="travel"
        values={values}
        // planChange={() => {}}
        onSubmit={this.handleSubmit}
        planDetails={planDetails}
        selectedPlanData={selectedPlanData}
        isLoggedIn={isLoggedIn}
        piggyCallback={piggyCallback}
        errorArray={errorArray}
        editForm={{
          displayOnly: true,
          valuesToListen: valuesToListen(values),
          conditions,
        }}
      >
        {this.renderForm()}
      </DetailForm>
    );
  }
}

TravelDetail.defaultProps = {
  detailValues: null,
  detailErrors: null,
  values: null,
};

TravelDetail.propTypes = {
  isLoggedIn: PropTypes.bool.isRequired,
  planDetails: PropTypes.object.isRequired,
  piggyCallback: PropTypes.func.isRequired,
  planData: PropTypes.object.isRequired,
  handleSubmit: PropTypes.func.isRequired,
  getIndividualFields: PropTypes.func.isRequired,
  selectedPlanData: PropTypes.object.isRequired,
  childFields: PropTypes.array.isRequired,
  adultFields: PropTypes.array.isRequired,
  valuesToListen: PropTypes.func.isRequired,
  conditions: PropTypes.object.isRequired,
  handlePostCode: PropTypes.func.isRequired,
  values: PropTypes.object,
  detailValues: PropTypes.object,
  detailErrors: PropTypes.object,
  onChangeDetailValue: PropTypes.func.isRequired,
};

const mapStateToProps = state => ({
  constants: state.insurance.constants,
});

export default connect(mapStateToProps)(TravelDetail);
