/**
 * ChangeInitialFormData.js
 * This is the green pencil button that appears mainly on
 * the compare view which will render the Edit Detail forms
 * of each category.
 *
 * Users will be able to change that form through this component
 * and will do a callback to update any changes, eg for compare view.
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import moment from 'moment';
import React, { Component, Fragment } from 'react';
// import { connect } from 'react-redux';
import { Modal, ModalHeader, Tooltip } from 'reactstrap';
import PropTypes from 'prop-types';

import EditButton from 'assets/images/forms/EditButton.svg';

import Button from 'components/FormsV3/Button/Button';

import { simplifyCost } from '../../../lib/helperFunctions';

import './ChangeInitialFormDataNew.scss';

class ChangeInitialFormDataNew extends Component {
  constructor(props) {
    super(props);

    this.state = {
      modal: false,
      revertFunc: () => {},
      isToolTip: false,
    };

    // this.renderHeader = this.renderHeader.bind(this);
    this.renderDetailValues = this.renderDetailValues.bind(this);
    this.renderEditButton = this.renderEditButton.bind(this);
    this.toggle = this.toggle.bind(this);
  }

  // componentDidMount() {
  //   const { resetSubmit } = this.props;

  //   resetSubmit();
  // }

  // shouldComponentUpdate(nextProps, nextState) {
  //   const {
  //     closePopUp,
  //     toolTipData,
  //     modal,
  //     isToolTip,
  //   } = this.state;
  //   const { resetSubmit } = this.props;

  //   if (nextProps.isEditSubmitted) {
  //     resetSubmit();
  //   }

  //   return nextProps.isEditSubmitted ||
  //         closePopUp !== nextState.closePopUp ||
  //         toolTipData !== nextState.toolTipData ||
  //         modal !== nextState.modal ||
  //         isToolTip !== nextState.isToolTip;
  // }

  toggle(revertFunc) {
    const { modal } = this.state;

    if (modal && revertFunc) {
      revertFunc();
    }

    this.setState({
      modal: !modal,
    });
  }

  // renderHeader() {
  //   const { headerValue, storeForm } = this.props;
  //   if (storeForm && headerValue) {
  //     const value = storeForm[headerValue] ? storeForm[headerValue] : '';
  //     return <span className="edit-detail-header">{value}</span>;
  //   }

  //   return null;
  // }

  renderDetailValues(isResp) {
    const { isToolTip } = this.state;
    const {
      values,
      valuesToListen,
      conditions,
    } = this.props;

    const conditonValue = conditions ? values[conditions.key] && values[conditions.key].value : null;
    const valuesArrayToListen = conditonValue ? conditions.values[conditonValue] : null;

    if (!values) {
      return null;
    }

    return (
      <div className="edit-detail-value-container-new">
        {
          _.reduce(valuesToListen, (result, data, index) => {
            if (
              !valuesArrayToListen ||
              (valuesArrayToListen && _.findIndex(valuesArrayToListen, value => value === data.value) !== -1)
            ) {
              const value = values[data.value] ? values[data.value].value : 'None';
              let displayValue = value;
              let isArray = false;

              if (data.customFunc) {
                displayValue = data.customFunc();
              } else if (data.extra === 'cost_simplify') {
                displayValue = simplifyCost(value);
              } else if (value.constructor === Array) {
                isArray = true;
                displayValue = [...value];
                if (data.aggregateDesc && displayValue.length > 1) {
                  displayValue = `${displayValue.length} ${data.aggregateDesc}`;
                }
              } else if (value.constructor === Object) {
                displayValue = value.display_name;
              } else if (value.constructor === Date) {
                displayValue = moment(value).format('DD MMM YYYY');
              }

              displayValue = displayValue.toString();

              let jsx = null;
              if (isResp) {
                const isLast = index === valuesToListen.length - 1;
                const comma = isLast ? '' : ',';
                jsx = (
                  <div key={`detail-${index}`} className="detail-resp-value">{displayValue}{comma}</div>
                );
              } else {
                jsx = (
                  <div className="edit-detail-value" key={`detail-${index}`}>
                    <img
                      src={data.logo}
                      alt={`${data.value}.svg`}
                      className="detail-logo"
                    />
                    <span className="detail-value">
                      {displayValue}
                      {
                        isArray && value.length > 1 ?
                          <Fragment>
                            <Tooltip
                              target={`initial-form-tooltip-${index}`}
                              isOpen={isToolTip}
                              toggle={() => this.setState({ isToolTip: false })}
                              className="animated fade"
                            >
                              {_.map(value, (v, idx) => <p key={`tooltip-value-${idx}`}>{v}</p>)}
                            </Tooltip>
                            <div
                              id={`initial-form-tooltip-${index}`}
                              className="tooltip-button"
                              onMouseMove={() => {
                                this.setState({
                                  isToolTip: true,
                                });
                              }}
                            >
                              <p>i</p>
                            </div>
                          </Fragment>
                        :
                          null
                      }
                    </span>
                  </div>
                );
              }
              result.push(jsx);
            }

            return result;
          }, [])
        }
      </div>
    );
  }

  renderEditButton(isResp) {
    if (isResp) {
      return (
        <div
          role="button"
          tabIndex={-1}
          onKeyDown={() => {}}
          onClick={() => {
            // this.popupRef.openPopup();
            this.toggle();
          }}
        >
          <img src={EditButton} alt="EditButton.svg" />
        </div>
      );
    }

    return (
      <Button
        variant="medium-outline-edit"
        height={40}
        onClick={() => {
          // this.popupRef.openPopup();
          this.toggle();
        }}
      >
        <p>Edit</p>
      </Button>
    );
  }

  render() {
    const { modal, revertFunc } = this.state;
    const { submitCallback, displayOnly } = this.props;

    if (displayOnly) {
      return (
        this.renderDetailValues(false)
      );
    }

    return (
      <div className="change-form-container-new">
        <div className="edit-detail-container d-none d-lg-flex">
          {/* {this.renderHeader()} */}
          <div className="edit-detail-inner-container">
            {this.renderDetailValues(false)}
          </div>
          {this.renderEditButton(false)}
        </div>
        <div className="edit-detail-resp-container d-flex d-lg-none">
          <div className="detail-inner-resp-container">
            {this.renderDetailValues(true)}
          </div>
          {this.renderEditButton(true)}
        </div>
        <Modal
          isOpen={modal}
          size="lg"
          backdropClassName="blue-modal-backdrop"
        >
          <ModalHeader toggle={() => this.toggle(revertFunc)} />
          {React.cloneElement(this.props.children, {
            submitCallback,
            closePopUp: (func = () => {}) => { this.toggle(); func(); },
            setCloseCallBack: (func = () => {}) => this.setState({ revertFunc: func }),
          })}
        </Modal>
        {/* <Popup ref={(r) => { this.popupRef = r; }}>
          {React.cloneElement(this.props.children, {
            submitCallback,
          })}
        </Popup> */}
      </div>
    );
  }
}

ChangeInitialFormDataNew.defaultProps = {
  submitCallback: null,
  conditions: null,
  headerValue: null,
  displayOnly: false,
  values: null,
};

ChangeInitialFormDataNew.propTypes = {
  valuesToListen: PropTypes.array.isRequired,
  // storeForm: PropTypes.object.isRequired,
  // isEditSubmitted: PropTypes.bool.isRequired,
  // resetSubmit: PropTypes.func.isRequired,
  submitCallback: PropTypes.func,
  conditions: PropTypes.object,
  headerValue: PropTypes.string,
  displayOnly: PropTypes.bool,
  values: PropTypes.object,
};

// const mapStateToProps = (state, ownProps) => ({
//   // isEditSubmitted: state.editForm.isEditSubmitted,
// });

// const mapDispatchToProps = dispatch => ({
// });

// connect(mapStateToProps, mapDispatchToProps)(ChangeInitialFormDataNew);

export default ChangeInitialFormDataNew;
