/**
 * DropDown.js
 * Custom form dropdown menu with input
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import document from 'global/document';
import window from 'global/window';
import _ from 'lodash';
import React, { Component } from 'react';
import { Input } from 'reactstrap';
import PropTypes from 'prop-types';

import { Scrollbars } from 'react-custom-scrollbars';

import FormInputWrapper from '../FormInputWrapper/FormInputWrapper';

import Arrow from '../../../assets/images/forms/arrow.svg';
import Cross from '../../../assets/images/forms/cross.svg';
import MagnifyGlass from '../../../assets/images/forms/magnifyGlass.svg';

import './DropDown.scss';
import './MultipleSelectDropDown.scss';

// How many items to display
const maxItemsDisplay = 5;
// Height for dropdown
const defaultHeight = 48;
// Padding for when multiple items overflow
const extraPaddingForMultiple = 5;
// Error text to display when error
const resultNullString = {
  display_name: 'No results found',
  value: null,
};

/**
 * Custom Form Dropdown class
 */
class DropDown extends Component {
  static getDerivedStateFromProps(nextProps, prevState) {
    const {
      dropList,
      numItemsDisplay,
    } = nextProps;

    if (!_.isEqual(dropList, prevState.initialListItems)) {
      const height = defaultHeight;
      const numItems = numItemsDisplay || maxItemsDisplay;
      const maxheight = (_.min([dropList ? dropList.length : 0, numItems]) + 1) * height;

      return {
        initialListItems: dropList,
        listItems: dropList,
        maxheight,
        listHeight: maxheight,
      };
    }

    return null;
  }
  /**
   * Constructor
   *
   * @param {object} props Component props
   */
  constructor(props) {
    super(props);

    // dropList - The passed down dropdown list items
    // placeholderString - Placeholder string for input
    // size - Size of this control
    // numItemsDisplay - Max items to display on view
    const {
      dropList,
      numItemsDisplay,
      value,
      isMultiple,
    } = this.props;

    // Get the control height
    const height = defaultHeight;
    // Get number of items to display
    const numItems = numItemsDisplay || maxItemsDisplay;
    // Get the max height of list
    const maxheight = (_.min([dropList ? dropList.length : 0, numItems]) + 1) * height;
    // isOpened - Whether dropdown is opened
    // isError - Whether there is input error
    // listItems - The actual dropdown list
    // selectedString - The selected item string
    // height - Preferred Control height
    // numItemsDisplay - How many items to display
    // maxheight - Max height of the list
    // listHeight - Height of the list. (Items count + control)
    // multipleSelectedElements - Array of multiple selected items
    // listElementsCounter - Up down selection position
    // clientHeight - Actual height of the control
    this.state = {
      isOpened: false,
      listItems: dropList,
      selectedString: (!isMultiple && value && value.display_name) || '',
      height,
      numItemsDisplay: numItems,
      maxheight,
      listHeight: maxheight,
      multipleSelectedElements: null,
      listElementsCounter: -1,
      clientHeight: height,
      extraPadding: 0,
      noTransition: true,
    };

    this.updateDimensions = this.updateDimensions.bind(this);
    this.handleClickOutside = this.handleClickOutside.bind(this);
    this.renderListItems = this.renderListItems.bind(this);
    this.renderTextInput = this.renderTextInput.bind(this);
    this.handleSelectClick = this.handleSelectClick.bind(this);
    this.handleOnInputChange = this.handleOnInputChange.bind(this);
    this.handleSetMultipleSelectedItem = this.handleSetMultipleSelectedItem.bind(this);
    this.handleDestroySelected = this.handleDestroySelected.bind(this);
    this.handleInputKeyPress = this.handleInputKeyPress.bind(this);
    this.handleItemKeyPress = this.handleItemKeyPress.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleExit = this.handleExit.bind(this);
    this.handleBlur = this.handleBlur.bind(this);
    this.handleFocus = this.handleFocus.bind(this);
  }

  /**
   * Lifecycle Method - componentDidMount
   */
  componentDidMount() {
    const { value, isMultiple } = this.props;

    // Add Handler to handle click outside of screen
    document.addEventListener('mousedown', this.handleClickOutside);
    // Update control height on resize in case it squeezes down
    window.addEventListener('resize', this.updateDimensions);
    // Update for the first time
    this.updateDimensions();

    const { overrideValue } = this.props;

    if (value) {
      if (isMultiple) {
        this.setState({
          multipleSelectedElements: value,
        });
      }
    }

    if (overrideValue) {
      this.handleChange(overrideValue);
    }

    this.setState({ noTransition: false });
  }

  /**
   * Lifecycle Method - componentDidUpdate
   */
  componentDidUpdate(prevProps) {
    const { multipleSelectedElements } = this.state;
    const { overrideValue, value, isMultiple } = this.props;

    // Use override mode if not using form
    if (!_.isEqual(overrideValue, prevProps.overrideValue)) {
      this.handleChange(overrideValue);
    }

    const isElements = !_.isEmpty(multipleSelectedElements);

    if (!_.isEqual(value, prevProps.value)) {
      this.setState({
        selectedString: (value && value.display_name) || '',
      });
    }

    if (value) {
      if (!isElements || (isElements && !_.isEqual(value, multipleSelectedElements))) {
        if (isMultiple) {
          this.setState({
            multipleSelectedElements: [...value],
          });
        }
      }
    }

    // For re-render fix where multiple changed to single
    if (isElements && !value) {
      this.setState({
        multipleSelectedElements: null,
      });
    }
    // Update control height if necessary, since the
    // height may change after render
    this.updateDimensions();
  }

  /**
   * Lifecycle Method - componentWillUnmount
   */
  componentWillUnmount() {
    // Remove Handler to handle click outside of screen
    document.removeEventListener('mousedown', this.handleClickOutside);
    window.removeEventListener('resize', this.updateDimensions);
  }

  /**
   * Get client height of the selection container
   * to determine the height of the whole control.
   * (For multiple only)
   *
   * @param {object} event
   */
  updateDimensions() {
    const { isMultiple } = this.props;

    // Only multiple will expand
    if (!isMultiple) {
      return;
    }

    const {
      clientHeight,
      height,
      extraPadding,
    } = this.state;

    let newHeight = defaultHeight;
    try {
      newHeight = this.selectedItemsContainer.children[this.selectedItemsContainer.childElementCount - 1].getBoundingClientRect().bottom - this.selectedItemsContainer.children[0].getBoundingClientRect().top;
      newHeight = newHeight < defaultHeight ? defaultHeight : newHeight + 10;
    } catch (error) {
      // eslint-disable-next-line no-empty
    }

    // if client height is not same as DOM, update with padding if expanded
    if (clientHeight !== newHeight) {
      this.setState({
        clientHeight: newHeight,
        extraPadding: newHeight - 2 * extraPadding > height ? extraPaddingForMultiple : 0,
      });
    }
  }

  handleChange(inputValue) {
    const {
      name,
      onChange,
      notForm,
      valueChangeCallback,
      isMultiple,
    } = this.props;

    if (notForm) {
      if (valueChangeCallback) {
        valueChangeCallback(inputValue);
      }
      this.inputField.value = inputValue.display_name;
      return;
    }

    onChange(isMultiple ? 'multiple_select' : 'select', name, inputValue);
  }

  /**
   * End selection if click on outside
   *
   * @param {object} event
   */
  handleClickOutside(event) {
    const { isOpened } = this.state;

    // If clicked outside, go to exit
    if (
      isOpened &&
      this.wrapperRef &&
      !this.wrapperRef.contains(event.target)
    ) {
      // Close it
      this.setState({
        isOpened: false,
      });

      // Unset from focus
      this.isFromFocus = false;
    }
  }

  /**
   * Handle on enter key
   *
   * @param {object} event
   */
  handleInputKeyPress(event) {
    const {
      isOpened,
      listElementsCounter,
      selectedString,
      multipleSelectedElements,
    } = this.state;

    const { isMultiple, placeholderString } = this.props;

    // If pressed on enter, go to exit
    if (event.key === 'Enter') {
      this.handleExit();
      event.preventDefault();
      return;
    }

    // Press down arrow from input
    if (event.key === 'ArrowDown') {
      if (isOpened) {
        if (listElementsCounter === -1) {
          event.preventDefault();
        }
        this.handleItemKeyPress(event);
      } else {
        this.setState({
          isOpened: true,
        });
      }
    }

    if (
      isMultiple &&
      event.key === 'Backspace' &&
      (selectedString === '' || selectedString === placeholderString) &&
      !_.isEmpty(multipleSelectedElements)
    ) {
      this.handleDestroySelected(multipleSelectedElements[multipleSelectedElements.length - 1]);
    }
  }

  /**
   * Key press on list items
   *
   * @param {object} event
   */
  handleItemKeyPress(event, inputValue) {
    const {
      listElementsCounter,
    } = this.state;

    let index = listElementsCounter;

    // If click enter on list item
    if (event.key === 'Enter') {
      // Focus input field
      // this.inputField.focus();
      // Handle exit with the item value
      this.handleExit(inputValue);
      event.preventDefault();
      return;
    }

    // Navigate list with up down arrow
    if (event.key === 'ArrowDown') {
      index = listElementsCounter + 1;
    } else if (event.key === 'ArrowUp') {
      index = listElementsCounter - 1;
    }

    // Cap index within list
    index = Math.max(0, Math.min(index, this.listElements.length - 1));
    // Focus on the next item
    this.listElements[index].focus();

    // If index changed, update state
    if (index !== listElementsCounter) {
      this.setState({
        listElementsCounter: index,
      });
    }

    event.stopPropagation();
  }

  /**
   * Handle toggling of dropdown list
   *
   * @param {bool} isOpenOnly - Only can open
   */
  handleSelectClick(isOpenOnly = false) {
    const {
      isOpened,
      selectedString,
      maxheight,
      multipleSelectedElements,
    } = this.state;

    const {
      dropList,
      isMultiple,
    } = this.props;

    // If this frame is not from focus
    if (!this.isFromFocus) {
      // Set open state
      if (!isOpenOnly || (isOpenOnly && !isOpened)) {
        this.setState({
          isOpened: !isOpened,
        });
      }

      // If previously invalid, reset list
      if (!selectedString || selectedString === resultNullString.display_name) {
        this.setState({
          listItems: dropList,
          listHeight: maxheight,
          listElementsCounter: -1,
        });

        if (!isMultiple || (isMultiple && _.isEmpty(multipleSelectedElements))) {
          this.handleChange(null);
        }
      }
    }

    // Unlock from focus
    this.isFromFocus = false;
  }

  /**
   * Handles selection of dropdown item
   *
   * @param {object} event - Click event
   * @param {string} item - Input string
   */
  handleSelectItemClick(event, item) {
    // If click on null item return
    if (item.value === resultNullString.value) {
      return;
    }

    const { numItemsDisplay, height } = this.state;
    const { dropList } = this.props;

    const {
      isMultiple,
    } = this.props;

    // Reset drop list items to all, set selected string,
    // and reset any errors
    this.setState({
      listItems: dropList,
      listHeight: (_.min([(dropList && dropList.length) || 0, numItemsDisplay]) + 1) * height,
    });

    // this.formParent.clearErrorString();

    if (isMultiple) {
      // If multiple dropdown, reset selected string
      this.setState({
        selectedString: '',
      });

      // Set input field text to empty
      this.inputField.value = '';
      // Refocus input bar
      // this.inputField.focus();

      // Set multiple selected item
      this.handleSetMultipleSelectedItem(item);
    } else {
      this.setState({
        selectedString: item.display_name,
      }, () => {
        // Toggle the dropdown menu
        this.handleSelectClick();

        // Update redux state
        this.handleChange(item);

        // Close dropdown
        this.setState({
          isOpened: false,
        });
      });
    }

    event.stopPropagation();
  }

  /**
   * Selection of multi select dropdown
   *
   * @param {string} item
   */
  handleSetMultipleSelectedItem(item) {
    const { multipleSelectedElements, height } = this.state;

    const newArr = multipleSelectedElements || [];

    // If item does not exists in the selected list
    if (!_.find(newArr, i => i.value === item.value)) {
      // Add item to list
      newArr.push(item);

      // Change state
      this.setState({
        multipleSelectedElements: newArr,
        clientHeight: this.selectedContainer ? this.selectedContainer.clientHeight : height,
        isOpened: false,
      });

      // Update redux store
      this.handleChange(newArr);
    }
  }

  /**
   * Destroys multi selected item
   *
   * @param {string} item
   */
  handleDestroySelected(item) {
    const { height } = this.state;

    const { multipleSelectedElements } = this.state;
    // Duplicate clean copy
    let newArr = multipleSelectedElements.slice();
    // Delete chosen item
    const index = newArr.indexOf(item);
    newArr.splice(index, 1);
    // Set null if empty
    newArr = newArr.length === 0 ? null : newArr;

    // Update state
    this.setState({
      multipleSelectedElements: newArr,
      clientHeight: this.selectedContainer ? this.selectedContainer.clientHeight : height,
    });

    // Update redux state
    this.handleChange(newArr);
    this.isFromFocus = false;
  }

  /**
   * Handles on input changes
   *
   * @param {object} event
   */
  async handleOnInputChange(event) {
    // Get current input value
    const { value } = event.target;

    const { dropList } = this.props;
    const { height, numItemsDisplay } = this.state;

    // Attempt to match value to list
    const result = _.filter(
      dropList,
      item => item.display_name.toString().toUpperCase().indexOf(value.toString().toUpperCase()) === 0,
    );

    // Get length of new list, min 1 which is error string
    const length = result.length ? result.length : 1;

    // Set new list items, height of dropdown list,
    // Force open list on type, and set selected string to value
    this.setState({
      listItems: result.length ? result : [resultNullString],
      listHeight: (_.min([length, numItemsDisplay]) + 1) * height,
      isOpened: true,
      selectedString: value,
      listElementsCounter: -1,
    });
  }

  /**
   * Handle Blur event
   *
   * @param {object} event
   */
  handleBlur(event) {
    // Check if focus event contains children
    const isChildren = this.wrapperRef.contains(event.relatedTarget);

    // If outside of children
    if (!isChildren) {
      // Close dropdown
      this.setState({
        isOpened: false,
      });
      // Handle exit case
      this.handleExit();
      this.isFromFocus = false;
    }
  }

  /**
   * Handle Focus event
   *
   * @param {object} event
   */
  handleFocus(event) {
    const { isOpened } = this.state;

    // Check if focus event contains children
    const isChildren = this.wrapperRef.contains(event.relatedTarget);
    // If not from children
    if (!isChildren) {
      // Open dropdown
      if (!isOpened) {
        this.handleSelectClick(true);
        // Prevent double click
        this.isFromFocus = true;
      }
    }
  }

  /**
   * Handle exit selection of entered input
   *
   * @param {string} inputValue - From selection
   */
  handleExit(inputValue = null) {
    const {
      selectedString,
      listItems,
    } = this.state;

    const {
      placeholderString,
      dropList,
      isMultiple,
    } = this.props;

    // Result string if match original list
    let resultValue = null;
    // If input causes an error
    let isError = false;
    // The string to use
    const finalString = inputValue !== null ? inputValue.display_name : selectedString;

    // If possible valid input
    if (
      finalString &&
      finalString !== placeholderString &&
      finalString !== ''
    ) {
      // Attempt to match finalString to original list
      listItems.some((obj) => {
        const result = obj.display_name.toString().toLowerCase() === finalString.toString().toLowerCase();
        if (result) {
          resultValue = obj;
        }

        return result;
      });

      // Check error if no match
      isError = !resultValue;
      // Set result string to match value or error value
      resultValue = isError ? null : resultValue;

      if (isMultiple) {
        if (!isError) {
          this.handleSetMultipleSelectedItem(resultValue);
        }

        this.inputField.value = '';
      } else {
        // Update redux value
        this.handleChange(resultValue);

        this.setState({
          isOpened: false,
        });
      }
    }

    // If not error, reset list to list all,
    // Set selected string and error if possible
    // and close the list
    this.setState({
      listItems: isError ? listItems : dropList,
      selectedString: (resultValue && resultValue.display_name) || '',
    });
  }

  /**
   * Renders the dropdown list items
   */
  renderListItems() {
    const { listItems, height } = this.state;
    this.listElements = [];

    const result = _.map(listItems, item => (
      <li key={item.value}>
        <div
          className="list-item text-style"
          style={{ height }}
          onClick={event => this.handleSelectItemClick(event, item)}
          role="button"
          tabIndex={-1}
          onKeyDown={(event) => { this.handleItemKeyPress(event, item); }}
          ref={(node) => { if (node !== null) this.listElements.push(node); }}
        >
          <p>{item.display_name}</p>
        </div>
      </li>
    ));

    return result;
  }

  /**
   * Render the multiple selected elements in the input container
   */
  renderMultipleSelectedElements() {
    const { multipleSelectedElements } = this.state;
    const { isMultiple } = this.props;

    if (!isMultiple) {
      return null;
    }

    return _.map(multipleSelectedElements, item => (
      <div className="selected-item-container" key={item.value}>
        <div className="selected-item">
          <p>{item.display_name}</p>
          <button
            type="button"
            className="selected-item-destroy"
            onMouseUp={() => this.handleDestroySelected(item)}
            tabIndex={-1}
          >
            <img src={Cross} alt="cross.svg" />
          </button>
        </div>
      </div>
    ));
  }

  renderTextInput() {
    const { selectedString } = this.state;
    const {
      isMultiple,
      isDisableTyping,
      placeholderString,
      name,
      notForm,
    } = this.props;

    return (
      <Input
        value={selectedString || ''}
        name={name}
        placeholder={((isMultiple || notForm) && placeholderString) || ''}
        autoComplete="off"
        ref={node => (this.inputField = node)}
        onChange={event => this.handleOnInputChange(event)}
        onKeyDown={event => this.handleInputKeyPress(event)}
        onClick={() => this.handleSelectClick(true)}
        disabled={isDisableTyping}
      />
    );
  }

  /**
   * Render component
   */
  render() {
    const {
      isOpened,
      listHeight,
      height,
      clientHeight,
      extraPadding,
      noTransition,
    } = this.state;

    // const isOpened = true;

    if (height && listHeight) {
      const {
        maxWidth,
        isMultiple,
        desc,
        extraInfo,
        errorString,
        noInternalDesc,
        logo,
        placeholderString,
        value,
      } = this.props;

      // Actual height of the whole control
      const actualheight = isOpened ? listHeight : height;
      // Actual height of only the list
      const actualListheight = actualheight - height;

      // Dynamic styles
      const dropDownOpenStyle = isOpened ? 'dropdown-container-opened' : 'dropdown-container-closed';
      const arrowStyle = isOpened ? 'angle-up' : 'angle-down';
      const transitionStyle = noTransition ? '' : 'do-transition';
      const listStyle = isOpened ? `list-opened ${transitionStyle}` : `list-closed ${transitionStyle}`;
      const multipleStyle = isMultiple || logo ? 'multi-drop-v3' : '';
      const isErrorStyle = errorString ? 'error' : '';

      const noInternalDescStyle = noInternalDesc ? 'no-internal-desc' : '';

      const titleStyle = value ? 'focused-title' : '';
      const marginTop = clientHeight > defaultHeight || isOpened ? clientHeight : 0;
      const marginBottom = clientHeight > defaultHeight ? 6 : 0;

      const logoImg = isMultiple ? MagnifyGlass : logo;
      const zIndexStyle = isOpened ? 'drop-open' : 'drop-close';

      return (
        <FormInputWrapper
          desc={desc}
          extraInfo={extraInfo}
          errorString={errorString}
        >
          <div
            className={`dropdown-container-v3 ${zIndexStyle} ${multipleStyle} ${noInternalDescStyle}`}
            style={{
              height: clientHeight,
              maxWidth,
              marginBottom,
            }}
            ref={node => (this.wrapperRef = node)}
            onBlur={event => this.handleBlur(event)}
            onFocus={event => this.handleFocus(event)}
          >
            <div
              className={`dropdown-container ${dropDownOpenStyle} ${isErrorStyle}`}
            >
              {
                isMultiple || noInternalDesc ? null :
                <div className={`title-header ${titleStyle}`}>
                  {placeholderString}
                </div>
              }
              <div
                className="selected-container"
                onKeyDown={event => this.handleInputKeyPress(event)}
                role="button"
                style={{ minHeight: height }}
                tabIndex={-1}
                ref={node => (this.selectedContainer = node)}
              >
                {
                  logoImg ?
                    <div className="icon-container" style={{ minHeight: height }}>
                      <img
                        src={logoImg}
                        alt="DropdownImage.svg"
                      />
                    </div>
                  :
                    null
                }
                <div
                  className="selected-items-container"
                  ref={node => this.selectedItemsContainer = node}
                  style={{ paddingTop: extraPadding, paddingBottom: extraPadding }}
                >
                  {this.renderMultipleSelectedElements()}
                  <div className="selected-text selected-text-multi">
                    {this.renderTextInput()}
                  </div>
                </div>
                <div
                  className="selected-arrow"
                  onClick={() => this.handleSelectClick()}
                  onKeyPress={() => {}}
                  role="button"
                  tabIndex={-1}
                >
                  <img
                    src={Arrow}
                    alt="arrow.svg"
                    className={`selected-arrow-style ${arrowStyle}`}
                  />
                </div>
              </div>
              <Scrollbars
                className={`list-container ${listStyle}`}
                style={{
                  minHeight: actualListheight,
                  marginTop,
                }}
                ref={node => this.listContainer = node}
                tabIndex={-1}
              >
                {
                  isOpened ?
                    this.renderListItems()
                  :
                    null
                }
              </Scrollbars>
            </div>
          </div>
        </FormInputWrapper>
      );
    }

    return null;
  }
}

DropDown.defaultProps = {
  maxWidth: '100%',
  numItemsDisplay: maxItemsDisplay,
  isMultiple: false,
  desc: null,
  isDisableTyping: false,
  extraInfo: null,
  notForm: false,
  overrideValue: null,
  errorString: null,
  dropList: null,
  valueChangeCallback: null,
  name: null,
  noInternalDesc: false,
  logo: null,
  value: null,
};

DropDown.propTypes = {
  dropList: PropTypes.array,
  placeholderString: PropTypes.string.isRequired,
  maxWidth: PropTypes.string,
  name: PropTypes.string,
  numItemsDisplay: PropTypes.number,
  isMultiple: PropTypes.bool,
  desc: PropTypes.string,
  isDisableTyping: PropTypes.bool,
  extraInfo: PropTypes.object,
  notForm: PropTypes.bool,
  overrideValue: PropTypes.string,
  errorString: PropTypes.string,
  valueChangeCallback: PropTypes.func,
  noInternalDesc: PropTypes.bool,
  logo: PropTypes.string,
  onChange: PropTypes.func.isRequired,
  value: PropTypes.oneOfType([PropTypes.object, PropTypes.array]),
};

export default DropDown;
