import React, { Component } from 'react';
import { Col } from 'reactstrap';
import PropTypes from 'prop-types';

import PiggyBank from 'assets/images/forms/PiggyBank.svg';

import './PiggySignUpNew.scss';

class PiggySignUpNew extends Component {
  render() {
    const {
      header,
      subHeader,
      onClick,
      isLoggedIn,
    } = this.props;

    return (
      <div
        className={`piggy-container-new ${isLoggedIn ? 'piggy-hovered' : 'piggy-hover'}`}
        onClick={onClick}
        role="button"
        tabIndex={-1}
        onKeyDown={null}
      >
        <Col className="d-flex d-sm-none wrap-container" xs={12}>
          <div className="piggy-img-container">
            <img src={PiggyBank} alt="Pig.svg" />
          </div>
          <div className="piggy-right-container">
            <div className="piggy-desc-container">
              {header}
              {subHeader}
            </div>
          </div>
        </Col>
        <Col className="d-none d-sm-flex wrap-container" xs={12}>
          <div className="piggy-img-container">
            <img src={PiggyBank} alt="Pig.svg" />
          </div>
          <div className="piggy-right-container">
            <div className="piggy-desc-container">
              {header}
              {subHeader}
            </div>
          </div>
        </Col>
      </div>
    );
  }
}

PiggySignUpNew.defaultProps = {
  onClick: () => {},
  isLoggedIn: false,
  subHeader: null,
};

PiggySignUpNew.propTypes = {
  header: PropTypes.object.isRequired,
  subHeader: PropTypes.object,
  onClick: PropTypes.func,
  isLoggedIn: PropTypes.bool,
};

export default PiggySignUpNew;
