/**
 * DropDown.js
 * Custom form dropdown menu with input
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import React, { Component } from 'react';
import PropTypes from 'prop-types';

import TickRounded from 'assets/images/forms/TickRounded.svg';

import './SingleCheckBox.scss';


/**
 * Custom Form Dropdown class
 */
class SingleCheckBox extends Component {
  render() {
    const {
      id,
      onChange,
      checked,
    } = this.props;

    const checkedStyle = checked ? 'single-check-box-checked' : 'single-check-box-non-checked';

    return (
      <div
        className={`single-check-box-container ${checkedStyle}`}
        id={id}
        onClick={onChange}
        checked={checked}
        tabIndex={-1}
        onKeyDown={null}
        role="button"
      >
        <div className="bg" />
        <div className="box">
          <img src={TickRounded} alt="Tick.svg" />
        </div>
        <p className="desc">
          {this.props.children}
        </p>
      </div>
    );
  }
}

SingleCheckBox.defaultProps = {
  checked: false,
};


SingleCheckBox.propTypes = {
  id: PropTypes.string.isRequired,
  onChange: PropTypes.func.isRequired,
  checked: PropTypes.bool,
};

export default SingleCheckBox;
