/**
 * RedirectPopupForm.js
 * The form view of the redirect component
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component } from 'react';
import { reduxForm } from 'redux-form';
import { Row, Col } from 'reactstrap';
import PropTypes from 'prop-types';

import Button from 'components/FormsV3/Button/Button';
import TextInput from '../../Forms/TextInput/TextInput';
import Selection from '../../Forms/Selection/Selection';

import { validateForm } from '../../../lib/validators';


class RedirectPopupForm extends Component {
  constructor(props) {
    super(props);

    this.state = {
      errorString: null,
    };

    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleSubmit(e) {
    const { onConfirm, category, noEmail } = this.props;
    const email = noEmail ? [] : ['email'];

    const errorResult = validateForm(e, [...email, 'disclaimer']);

    if (!noEmail && errorResult.email) {
      this.setState({
        errorString: 'Invalid email!',
      });
      return;
    }

    if (_.isEmpty(e.disclaimer)) {
      this.setState({
        errorString: 'Please agree to the terms to proceed.',
      });
      return;
    }

    if (!_.isEmpty(errorResult)) {
      this.setState({
        errorString: 'Please fill in all fields!',
      });

      return;
    }

    this.setState({
      errorString: null,
    });

    onConfirm({ ...e, category });
  }

  render() {
    const { errorString } = this.state;
    const {
      handleSubmit,
      form,
      change,
      noEmail,
    } = this.props;

    return (
      <form onSubmit={handleSubmit(this.handleSubmit)}>
        <div className="mid-container">
          <Row noGutters>
            {
              noEmail ?
                null
              :
                <Col xs={12}>
                  <TextInput
                    form={form}
                    change={change}
                    name="email"
                    placeholderString=""
                    size="medium"
                    zIndex={99}
                    desc="Email Address"
                  />
                </Col>
            }
            <Col xs={12}>
              <Selection
                form={form}
                change={change}
                name="disclaimer"
                type="checkbox"
                options={[
                  { key: 'disclaimer', description: 'I agree to store this policy in my PolicyPal account after purchase, in accordance with the Personal Data Protection Act.' },
                ]}
                sizeInSM={12}
                desc=""
              />
            </Col>
            {
              errorString ?
                <Col xs={12}>
                  <p className="error-string">{errorString}</p>
                </Col>
              :
                null
            }
          </Row>
        </div>
        <div className="button-container">
          <Button
            type="submit"
            className="checkout-button"
            variant="medium-flat-green"
            width={166}
            height={72}
          >
            <p>Confirm</p>
          </Button>
        </div>
      </form>
    );
  }
}

RedirectPopupForm.defaultProps = {
  category: null,
  noEmail: false,
};

RedirectPopupForm.propTypes = {
  onConfirm: PropTypes.func.isRequired,
  handleSubmit: PropTypes.func.isRequired,
  form: PropTypes.string.isRequired,
  change: PropTypes.func.isRequired,
  category: PropTypes.string,
  noEmail: PropTypes.bool,
};

export default reduxForm({
  form: 'redirect-form',
})(RedirectPopupForm);
