/**
 * RedirectPopup.js
 * Popup to inform users that we are redirecting
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';

import RedirectPopupForm from './RedirectPopupForm';
import Popup from '../Popup/Popup';

import './RedirectPopup.scss';

class RedirectPopup extends Component {
  constructor(props) {
    super(props);

    this.state = {
      category: null,
    };

    this.openRedirectPopup = this.openRedirectPopup.bind(this);
  }

  openRedirectPopup(category) {
    this.setState({
      category,
    });
    setTimeout(this.popupRef.openPopup, 100);
  }

  render() {
    const { category } = this.state;
    const { onConfirm, noEmail } = this.props;

    return (
      <Popup ref={(r) => { this.popupRef = r; }}>
        <Fragment>
          <div className="redirect-popup">
            <div className="upper-container">
              <p>{'Kindly fill up the following section before we redirect you to the insurer\'s page. The details below will be used for verification with the insurer after purchase.'}</p>
            </div>
            <RedirectPopupForm
              noEmail={noEmail}
              closPopup={() => this.popupRef.closePopup()}
              onConfirm={onConfirm}
              category={category}
            />
          </div>
        </Fragment>
      </Popup>
    );
  }
}

RedirectPopup.defaultProps = {
  noEmail: false,
};

RedirectPopup.propTypes = {
  onConfirm: PropTypes.func.isRequired,
  noEmail: PropTypes.bool,
};

export default RedirectPopup;

