import React, { Component } from 'react';
import PropTypes from 'prop-types';

import PiggySignUp from './PiggySignUpNew';

class PiggyFillFormNew extends Component {
  render() {
    const {
      onClick,
      category,
      mainText,
      descText,
      isLoggedIn,
      cashback,
    } = this.props;

    let mainTextToUse = mainText;
    let descTextToUse = descText;

    if (!mainTextToUse) {
      mainTextToUse = category === 'life' || category === 'endowment' || category === 'retirement' ?
        'Earn extra $5 Credits instantly!' :
        'Sign in to get 5% Cashback on your purchase!';
    }

    if (!descTextToUse) {
      descTextToUse = category === 'life' || category === 'endowment' || category === 'retirement' ?
        'Submit the form below as a PolicyPal user to earn $5 Credits in your account.' :
        'Purchase as a PolicyPal user to earn 5% Cashback in your account within 14 days.';
    }

    if (isLoggedIn && cashback) {
      mainTextToUse = `Proceed to earn ${cashback} cashback now!`;
      descTextToUse = null;
    }


    return (
      <PiggySignUp
        isLoggedIn={isLoggedIn}
        header={
          <p className="main-text">{mainTextToUse}</p>
        }
        subHeader={
          descTextToUse && <p className="desc-text">{descTextToUse}</p>
        }
        onClick={!isLoggedIn && onClick ? onClick : null}
      />
    );
  }
}

PiggyFillFormNew.defaultProps = {
  onClick: () => {},
  mainText: null,
  descText: null,
  isLoggedIn: false,
  cashback: null,
};

PiggyFillFormNew.propTypes = {
  onClick: PropTypes.func,
  category: PropTypes.string.isRequired,
  mainText: PropTypes.string,
  descText: PropTypes.string,
  isLoggedIn: PropTypes.bool,
  cashback: PropTypes.string,
};

export default PiggyFillFormNew;
